      buttonTypes: [
        "primary",
        "default",
        "dashed",
        "text",
        "info",
        "success",
        "warning",
        "error"
      ]
    };
  },

  methods: {
    getData() {
      if (this.tableData.search) {
        this.tableData.searchData = this.$refs.searchForm.getData();
      }
      
      return this.tableData;
    },

    addColumns(columns) {
      let arr = this.columnsArr;
      arr = arr.trim();
      arr = arr.split(",");
      let newClumns = [];
      arr.forEach(item => {
        let p = item.split("|");
        columns.push({
          type: "",
          title: p[0],
          key: p[1],
          options: []
        });
      });
    },

    delItem(o, index) {
      o.splice(index, 1);
    },

    addButton(o) {
      o.push({
        name: "",
        type: "",
        ghost: false
      });
    },

    addTableItem(o) {
      o.push({
        key: "",
        title: ""
      });
    }
  }
};
