<template>
  <div>
    <div v-if="type=='form'">
      表单类型：
      <RadioGroup v-model="formData.formType">
        <Radio label="form">普通表单</Radio>
        <Radio label="search">搜索</Radio>
      </RadioGroup>
    </div>

    <div v-if="type=='form'" class="dynamic-search">
     model变量名：
      <Input placeholder="如不输自动生成" v-model="formData.dataName" />
    </div>
    <div style="text-align:right;padding-bottom:5px;">
      <Button
        type="primary"
        v-if="formData.formItem.length===0"
        @click="addFormItem(formData.formItem)"
      >新增</Button>
    </div>
    <table class="FEtable">
      <tr>
        <th>表单元素配置</th>

        <th style="text-align:center;width:160px">操作</th>
      </tr>
      <tr
        v-for="(item, index) in formData.formItem"
        class="dynamic-module-item"
        draggable="true"
        @dragstart="dragstart($event, index)"
        @dragend="dragend(formData.formItem)"
        @dragenter="dragenter($event,index)"
      >
        <td>
          <Select
            clearable
            v-model="item.type"
            placeholder="控件类型"
            @on-change="changeFormItemType($event, index, formData.formItem)"
          >
            <Option v-for="type in formTypes" :value="type" :key="type">{{ type }}</Option>
          </Select>

          <template v-if="item.type">
            <Input v-model="item.label" placeholder="label" clearable />
            <Input v-model="item.key" style="width:80px" placeholder="key" clearable />
            <span v-if="item.type==='text'">
              <Input v-model="item.text" style="width:80px" placeholder="text" clearable />
            </span>
            <template v-if="hasParm(item.type, 'placeholder')">
              <Input v-model="item.placeholder" placeholder="placeholder" clearable />
            </template>
            <template v-if="hasParm(item.type, 'dataName')">
              <Input placeholder="数据名称,如不输自动生成" v-model="item.dataName" clearable />
            </template>
            <CheckboxGroup v-model="item.options">
              <Checkbox v-if="hasParm(item.type, 'clearable')" key="clearable" label="clearable">可清空</Checkbox>
              <Checkbox key="labelGroup" label="labelGroup">label组</Checkbox>
              <Checkbox v-if="hasParm(item.type, 'multiple')" key="multiple" label="multiple">多选</Checkbox>
              <Checkbox
                v-if="hasParm(item.type, 'filterable')"
                key="filterable"
                label="filterable"
              >可搜索</Checkbox>
              <Checkbox v-if="hasParm(item.type, 'compass')" key="compass" label="compass">组合</Checkbox>
            </CheckboxGroup>
          </template>
          <div v-if="formData.formType==='form'">
            <br />验证相关：
            <Checkbox v-model="item.validate.required">required</Checkbox>
            <Select clearable v-model="item.validate.type" placeholder="验证类型">
              <Option v-for="type in validateTypes" :value="type" :key="type">{{ type }}</Option>
            </Select>
            <Input
              v-model="item.validate.message"
              style="width:400px"
              placeholder="错误提示"
              clearable
            />
          </div>
        </td>
        <td>
          <Button type="error" ghost @click="delItem(formData.formItem, index)">del</Button>&nbsp;&nbsp;
          <Button
            type="primary"
            v-if="index === formData.formItem.length-1"
            @click="addFormItem(formData.formItem)"
          >add</Button>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
import mixin from "./mixin";
export default {
  name: "DFORM",
  mixins: [mixin],
  props: {
    type: ""
  },
  data() {
    return {
      optionCase: {
        placeholder: [
          "input",
          "select",
          "date",
          "daterange",
          "time",
          "timerange",
          "year",
          "month",
          "inputNumber",
          "password",
          "textarea"
        ],
        clearable: [
          "input",
          "select",
          "date",
          "daterange",
          "time",
          "timerange",
          "year",
          "month",
          "password",
          "textarea"
        ],
        multiple: ["select"],
        filterable: ["select"],
        compass: ["radio", "checkbox"],
        dataName: ["select"]
      },
      validateTypes: [
        "string",
        "number",
        "array",
        "object",
        "boolean",
        "email",
        "url",
        "date"
      ],

      formTypes: [
        "text",
        "input",
        "select",
        "radio",
        "checkbox",
        "textarea",
        "switch",
        "date",
        "daterange",
        "year",
        "month",
        "time",
        "timerange",
        "inputNumber",
        "password"
      ],

      formData: {
        name: "表单",
        type: "form",
        formType: "form",
        dataName: "",
        formItem: [
          {
            type: "",
            label: "",
            key: "",
            placeholder: "",
            dataName: "",
            options: ["clearable"],
            validate: {
              required: true,
              type: "",
              message: "",
              trigger: ""
            }
          }
        ]
      }
    };
  },

  methods: {
    getData() {
      return this.formData;
    },
    addFormItem(o) {
      o.push({
        type: "",
        label: "",
        key: "",
        placeholder: "",
        options: [],
        validate: {
          required: true,
          type: "",
          message: "",
          trigger: ""
        }
      });
    },

    delItem(o, index) {
      o.splice(index, 1);
    },

    hasParm(type, key) {
      let arr = this.optionCase[key];
      if (arr.indexOf(type) !== -1) {
        return true;
      }
      return false;
    },

    changeFormItemType(type, index, items) {
      let basePrm = {
        type: type,
        label: "",
        key: "",
        placeholder: "",
        dataName: "",
        options: [],
        validate: {
          required: true,
          type: "",
          message: "",
          trigger: ""
        }
      };
      if (this.hasParm(type, "clearable")) {
        basePrm.options.push("clearable");
      }
      if (this.hasParm(type, "compass")) {
        basePrm.options.push("compass");
      }
      this.$set(items, index, basePrm);
    }
  },
  mounted() {
    console.log(this.type);
    if (this.type === "search") {
      this.formData.formType = "search";
    }
  }
};
</script>
