let mixin = {
    data() {
        return {
            index: 0,
            rIndex: 0,

        }
    },
    methods: {

        dragstart(e, index) {
            this.index = index;
        },

        dragend(list) {
            if (this.rIndex == this.index) {
                return;
            }
            var item = list[this.index];
            list.splice(this.index, 1);
            list.splice(this.rIndex, 0, item);
        },

        dragenter(e, index) {
            this.rIndex = index;
        }

    },

    mounted() {

    }

}

export default mixin